.profile-pic {
  width: 90%;
  height: auto;
  margin: 5px;
  border-radius: 50%;
  border: 10px solid #333;
  box-shadow: inset 0 0 10px black, 0 4px 8px rgba(0, 0, 0, 0.4);
}

.summary {
  padding: 16px !important;
}
@media screen and (max-width: 767px) {
  .summary {
    padding-bottom: 64px !important;
  }
  .main-row {
    justify-content: start !important;
  }
}
